@use 'base/variables' as v;
@use 'base/mixins' as m;

.header__barra {
    display: flex;
    flex-direction: column;
    @include m.tablet {
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
    }
}

.header__logo {
    width: 50%;
    margin: 0 auto;
    @include m.tablet {
        width: 20rem;
        margin: 0;
    }
}